import axios from 'axios';
import qs from 'qs';
import {
  Message,
  Loading
} from 'element-ui'
import router from '@/router';
import { delCookie } from '@/utils/base.js';

//axios.defaults.timeout = 5000;                        //å“åº”æ—¶é—´
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'; //é…ç½®è¯·æ±‚å¤´
//axios.defaults.baseURL = 'http://www.jjj-shop.com/';   //é…ç½®æŽ¥å£åœ°å€
axios.defaults.baseURL = '/index.php'; //é…ç½®æŽ¥å£åœ°å€
axios.defaults.withCredentials = true;
axios.defaults.responseType = 'json';

//POSTä¼ å‚åºåˆ—åŒ–(æ·»åŠ è¯·æ±‚æ‹¦æˆªå™¨)
axios.interceptors.request.use((config) => {
  //åœ¨å‘é€è¯·æ±‚ä¹‹å‰åšæŸä»¶äº‹
  if (config.method === 'post') {
    config.data = qs.stringify(config.data);
  }
  return config;
}, (error) => {
  console.log('é”™è¯¯çš„ä¼ å‚')
  return Promise.reject(error);
});

//è¿”å›žçŠ¶æ€åˆ¤æ–­(æ·»åŠ å“åº”æ‹¦æˆªå™¨)
axios.interceptors.response.use((res) => {
  //æœªç™»é™†
  if (res.data.code !== 1) {
    console.log('æœªç™»å½•çŠ¶æ€')
    if(res.data.code===0){
      Message({
        showClose: true,
        message: res.data.msg,
        type: "error"
      });
      return Promise.reject(res.data);
    }else{
      delCookie('isLogin');
      router.push({
      	path: '/login',
      	query: {
      	}
      })
    }
  }else{
    return res.data;
  }
}, (error) => {
  Message({
    showClose: true,
    message: 'æŽ¥å£è¯·æ±‚å¼‚å¸¸ï¼Œè¯·ç¨åŽå†è¯•~',
    type: "error"
  });
  return Promise.reject(error);
});

/**
 * è¿”å›žä¸€ä¸ªPromise(å‘é€postè¯·æ±‚)
 * errorbackæ˜¯å¦é”™è¯¯å›žè°ƒ
 */
export function _post(url, params, errorback) {
  return new Promise((resolve, reject) => {
    axios.post(url, params)
      .then(response => {
        resolve(response);
      })
      .catch((error) => {
        errorback && reject(error);
      })
  })
}

/**
 * è¿”å›žä¸€ä¸ªPromise(å‘é€getè¯·æ±‚)
 * errorbackæ˜¯å¦é”™è¯¯å›žè°ƒ
 */
export function _get(url, param, errorback) {
  return new Promise((resolve, reject) => {
    axios.get(url, {
        params: param
      })
      .then(response => {
        resolve(response)
      })
      .catch((error) => {
        errorback && reject(error);
      })
  })
}
export default {
  _post,
  _get,
}



